h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 2.8rem;
}

h2 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
  color: #111;
  line-height: 1.8;
}

a {
  color: #767676;
  text-decoration: underline;
}

.description {
  text-align: center;
}
.description img {
  height: 4.5rem;
  margin-bottom: 2rem;
}